import { Router } from 'express'
import { Op, QueryTypes } from 'sequelize'
import sequelize from '../models/sequelize'
import toRes from '../lib/toRes'
import XuanjianghuiModel from '../models/XuanjianghuiModel'
import util from '../lib/util'
import jwt from 'jsonwebtoken'

export default ({ config, db }) => {
	let api = Router()

	// 分页接口（后端）
	api.get('/page', async (req, res) => {

		try {

			let page = parseInt(req.query.page) || 1
			let limit = parseInt(req.query.limit) || 10
			let sort = req.query.sort || 'id'
			let order = req.query.order || 'asc'

			let where = {}
			let biaoti = req.query.biaoti
			if (biaoti) {//标题

				if (biaoti.indexOf('%') != -1) {
					where.biaoti = {
						[Op.like]: biaoti
					}
				} else {
					where.biaoti = {
						[Op.eq]: biaoti
					}
				}
			}
			let fengmian = req.query.fengmian
			if (fengmian) {//封面

				if (fengmian.indexOf('%') != -1) {
					where.fengmian = {
						[Op.like]: fengmian
					}
				} else {
					where.fengmian = {
						[Op.eq]: fengmian
					}
				}
			}
			let zhaopinzhiwei = req.query.zhaopinzhiwei
			if (zhaopinzhiwei) {//招聘职位

				if (zhaopinzhiwei.indexOf('%') != -1) {
					where.zhaopinzhiwei = {
						[Op.like]: zhaopinzhiwei
					}
				} else {
					where.zhaopinzhiwei = {
						[Op.eq]: zhaopinzhiwei
					}
				}
			}
			let zhaopinrenshu = req.query.zhaopinrenshu
			if (zhaopinrenshu) {//招聘人数

				if (zhaopinrenshu.indexOf('%') != -1) {
					where.zhaopinrenshu = {
						[Op.like]: zhaopinrenshu
					}
				} else {
					where.zhaopinrenshu = {
						[Op.eq]: zhaopinrenshu
					}
				}
			}
			let jubandidian = req.query.jubandidian
			if (jubandidian) {//举办地点

				if (jubandidian.indexOf('%') != -1) {
					where.jubandidian = {
						[Op.like]: jubandidian
					}
				} else {
					where.jubandidian = {
						[Op.eq]: jubandidian
					}
				}
			}
			let jubanshijian = req.query.jubanshijian
			if (jubanshijian) {//举办时间

				if (jubanshijian.indexOf('%') != -1) {
					where.jubanshijian = {
						[Op.like]: jubanshijian
					}
				} else {
					where.jubanshijian = {
						[Op.eq]: jubanshijian
					}
				}
			}
			let xuanjianghuixiangqing = req.query.xuanjianghuixiangqing
			if (xuanjianghuixiangqing) {//宣讲会详情

				if (xuanjianghuixiangqing.indexOf('%') != -1) {
					where.xuanjianghuixiangqing = {
						[Op.like]: xuanjianghuixiangqing
					}
				} else {
					where.xuanjianghuixiangqing = {
						[Op.eq]: xuanjianghuixiangqing
					}
				}
			}
			let qiyezhanghao = req.query.qiyezhanghao
			if (qiyezhanghao) {//企业账号

				if (qiyezhanghao.indexOf('%') != -1) {
					where.qiyezhanghao = {
						[Op.like]: qiyezhanghao
					}
				} else {
					where.qiyezhanghao = {
						[Op.eq]: qiyezhanghao
					}
				}
			}
			let qiyemingcheng = req.query.qiyemingcheng
			if (qiyemingcheng) {//企业名称

				if (qiyemingcheng.indexOf('%') != -1) {
					where.qiyemingcheng = {
						[Op.like]: qiyemingcheng
					}
				} else {
					where.qiyemingcheng = {
						[Op.eq]: qiyemingcheng
					}
				}
			}
			let lianxidianhua = req.query.lianxidianhua
			if (lianxidianhua) {//联系电话

				if (lianxidianhua.indexOf('%') != -1) {
					where.lianxidianhua = {
						[Op.like]: lianxidianhua
					}
				} else {
					where.lianxidianhua = {
						[Op.eq]: lianxidianhua
					}
				}
			}
			//表名
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'qiye') {
				where.qiyezhanghao = {
					[Op.eq]: req.session.userinfo === undefined ? jwt.decode(req.headers.token).username : req.session.userinfo.qiyezhanghao
				}
				if (where['userid'] != undefined) {
					delete where.userid
				}
			}

			let result = await XuanjianghuiModel.findAndCountAll({
				order: [[sort, order]],
				where,
				offset: (page - 1) * limit,
				limit
			})
			
			result.currPage = page
			result.pageSize = limit

			toRes.page(res, 0, result)
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 分页接口（前端）
	api.get('/list', async (req, res) => {

		try {

			let page = parseInt(req.query.page) || 1
			let limit = parseInt(req.query.limit) || 10
			let sort = req.query.sort || 'id'
			let order = req.query.order || 'asc'

			let where = {}
			let biaoti = req.query.biaoti
			if (biaoti) {

				if (biaoti.indexOf('%') != -1) {
					where.biaoti = {
						[Op.like]: biaoti
					}
				} else {
					where.biaoti = {
						[Op.eq]: biaoti
					}
				}
			}
			let jubandidian = req.query.jubandidian
			if (jubandidian) {

				if (jubandidian.indexOf('%') != -1) {
					where.jubandidian = {
						[Op.like]: jubandidian
					}
				} else {
					where.jubandidian = {
						[Op.eq]: jubandidian
					}
				}
			}
			let qiyemingcheng = req.query.qiyemingcheng
			if (qiyemingcheng) {

				if (qiyemingcheng.indexOf('%') != -1) {
					where.qiyemingcheng = {
						[Op.like]: qiyemingcheng
					}
				} else {
					where.qiyemingcheng = {
						[Op.eq]: qiyemingcheng
					}
				}
			}

			let result = await XuanjianghuiModel.findAndCountAll({
				order: [[sort, order]],
				where,
				offset: (page - 1) * limit,
				limit
			})
			
			result.currPage = page
			result.pageSize = limit

			toRes.page(res, 0, result)
		} catch(err) {
			toRes.session(res, 401, '您的权限不够！', '', 200)
		}
	})


	// 保存接口（后端）
	api.post('/save', async (req, res) => {

		try {

			Object.keys(req.body).forEach(item=>{
				if(req.body[item] == '')  delete req.body[item]
			})

			const userinfo = await XuanjianghuiModel.create(req.body)

			if (userinfo === null) {
				toRes.session(res, -1, '添加失败！')
			} else {
				toRes.session(res, 0, '添加成功！')
			}
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 保存接口（前端）
	api.post('/add', async (req, res) => {

		try {

			Object.keys(req.body).forEach(item=>{
				if(req.body[item] == '')  delete req.body[item]
			})

			if (jwt.decode(req.headers.token) == null) {
				toRes.session(res, 401, '请登录后再操作', '', 401)
			}

			const userinfo = await XuanjianghuiModel.create(req.body)

			if (userinfo === null) {
				toRes.session(res, -1, '添加失败！')
			} else {
				toRes.session(res, 0, '添加成功！')
			}
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 更新接口
	api.post('/update', async (req, res) => {

		try {

			await XuanjianghuiModel.update(req.body, {
				where: {
				  id: req.body.id || 0
				}
			})
			toRes.session(res, 0, '编辑成功！')
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 删除接口
	api.post('/delete', async (req, res) => {

		try {

			await XuanjianghuiModel.destroy({
				where: {
				  id: {
					[Op.in]: req.body
				  }
				}
			})
			toRes.session(res, 0, '删除成功！')
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 详情接口（后端）
	api.all('/info/:id', async (req, res) => {

		try {
			toRes.record(res, 0, await XuanjianghuiModel.findOne({ where: { id: req.params.id } }))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 详情接口（前端）
	api.all('/detail/:id', async (req, res) => {

		try {
			toRes.record(res, 0, await XuanjianghuiModel.findOne({ where: { id: req.params.id } }))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 获取需要提醒的记录数接口
	api.get('/remind/:columnName/:type', async (req, res) => {

		try {

			let sql = 'SELECT 0 AS count'
			
			if (req.params.type == 1) {
				if (req.query.remindstart) sql = "SELECT COUNT(*) AS count FROM xuanjianghui WHERE " + req.params.columnName + " >= '" + req.query.remindstart + "'"
				if (req.query.remindend) sql = "SELECT COUNT(*) AS count FROM xuanjianghui WHERE " + req.params.columnName + " <= '" + req.query.remindend + "'"

				if (req.query.remindstart && req.query.remindend) {
					sql = "SELECT COUNT(*) AS count FROM xuanjianghui WHERE " + req.params.columnName + " >= '" + req.query.remindstart + "' AND " + req.params.columnName + " <= '" + req.query.remindend + "'"
				}
			}

			if (req.params.type == 2) {
				if (req.query.remindstart) {
					let remindStart = util.getDateTimeFormat(0 + Number(req.query.remindstart), "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM xuanjianghui WHERE " + req.params.columnName + " >= '" + remindStart + "'"
				}
				if (req.query.remindend) {
					let remindEnd = util.getDateTimeFormat(req.query.remindend, "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM xuanjianghui WHERE " + req.params.columnName + " <= '" + remindEnd + "'"
				}

				if (req.query.remindstart && req.query.remindend) {
					let remindStart = util.getDateTimeFormat(0 + Number(req.query.remindstart), "yyyy-MM-dd")
					let remindEnd = util.getDateTimeFormat(req.query.remindend, "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM xuanjianghui WHERE " + req.params.columnName + " >= '" + remindStart + "' AND " + req.params.columnName + " <= '" + remindEnd + "'"
				}
			}

			const results = await sequelize.query(sql, {
				plain: true,
				raw: true,
				type: QueryTypes.SELECT
			})

			toRes.count(res, 0, results.count)
		} catch(err) {
			
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 分组统计接口
	api.get('/group/:columnName', async (req, res) => {

		try {

			let sql = ""
			let columnName = req.params.columnName
			let where = " WHERE 1 = 1 "
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'qiye') {
				where += " AND qiyezhanghao = '" + jwt.decode(req.headers.token).username + "' ";
			}
			sql = "SELECT COUNT(*) AS total, " + columnName + " FROM xuanjianghui " + where + " GROUP BY " + columnName + " LIMIT 10" 
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 统计指定字段
	api.get('/value/:xColumnName/:yColumnName', async (req, res) => {

		try {

			let sql = ""
			let xColumnName = req.params.xColumnName
			let yColumnName = req.params.yColumnName
			let where = " WHERE 1 = 1 "
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'qiye') {
				where += " AND qiyezhanghao = '" + jwt.decode(req.headers.token).username + "' ";
			}
			
			sql = "SELECT " + xColumnName + ", SUM(" + yColumnName + ") AS total FROM xuanjianghui " + where + " GROUP BY " + xColumnName + " DESC LIMIT 10"
			
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 按日期统计
	api.get('/value/:xColumnName/:yColumnName/:timeStatType', async (req, res) => {

		try {
			
			let sql = ""
			let xColumnName = req.params.xColumnName
			let yColumnName = req.params.yColumnName
			let timeStatType = req.params.timeStatType
			let tableName = "xuanjianghui"
			let where = " WHERE 1 = 1 "
			if (jwt.decode(req.headers.token).role != '管理员') {
				where += " AND qiyezhanghao = '" + jwt.decode(req.headers.token).username + "' ";
			}

            if (config.dbConnection.dbtype.toLowerCase() == "mysql") {
                if (timeStatType == "日")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y-%m-%d') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y-%m-%d') LIMIT 10";
                if (timeStatType == "月")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y-%m') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y-%m')  LIMIT 10";
                if (timeStatType == "年")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y')  LIMIT 10";
            } else {
                if (timeStatType == "日")
                    sql = "SELECT DATE_FORMAT(VARCHAR(10)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(10)," + xColumnName + ", 120)  LIMIT 10";
                if (timeStatType == "月")
                    sql = "SELECT DATE_FORMAT(VARCHAR(7)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(7)," + xColumnName + ", 120)  LIMIT 10";
                if (timeStatType == "年")
                    sql = "SELECT DATE_FORMAT(VARCHAR(4)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(4)," + xColumnName + ", 120)  LIMIT 10";
            }
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})


	return api
}
